.site-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  padding: 1rem;

  border-bottom: 1px solid rgb(230, 230, 230);

  @media(min-width: 80rem) {
    padding: 2rem 10rem;
  }
}

.site-header-overflow {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.action {
  display: inline-block;
  padding: 0.5rem 1rem;

  background: #1b212f;

  border-radius: 0.2rem;

  &:hover {
    background: rgb(0, 100, 206);
  }

  &,
  &:link,
  &:active,
  &:visited {
    text-decoration: none;
  }
}

.action:link,
.action:active,
.action:visited {
  color: white;
}

.site-nav {
  display: grid;
  grid-auto-flow: column;
  gap: 4rem;

  >a:link,
  >a:active,
  >a:visited {
    color: black;
  }

  a:hover {
    text-decoration: none;
  }
}

.alert {
  display: block;
  padding: 0.25rem 1rem;

  background: #1b212f;

  text-align: center;

  &,
  &:link,
  &:active,
  &:visited {
    color: white;
  }

  &:hover {
    text-decoration: none;
  }
}
