$primary-color: rgb(0, 100, 206);
$primary-color: #1b212f;

.button {
  display: inline-block;
  padding: 0.5rem 1rem;

  white-space: nowrap;
  line-height: 2;

  text-transform: uppercase;

  //background: linear-gradient(180deg, #ffffff, #e8e8e8);
  background: rgb(235, 235, 235);
  background: #1b212f;

  border: 1px solid #515052;
  border: none;
  border-radius: 0.2rem;

  font-weight: bold;
  white-space: nowrap;

  cursor: pointer;

  &,
  &:link,
  &:visited {
    color: white;
  }

  &:focus {
    //outline: none;
    //box-shadow: 0 0 0 5px rgba($primary-color, 0.1);
    //color: white;
  }
}

.button-primary {
  background: linear-gradient(180deg, lighten($primary-color, 10%), $primary-color);
  border-color: darken($primary-color, 10%);

  &,
  &:link,
  &:visited {
    color: white;
  }

  &:focus {
    color: white;
    border-color: darken($primary-color, 10%);
  }
}

.button-danger {
  background: linear-gradient(180deg, lighten(red, 20%), red);
  border-color: darken(red, 20%);

  &,
  &:link,
  &:visited {
    color: white;
  }

  &:focus {
    color: white;
    border-color: darken($primary-color, 10%);
  }
}

.button:disabled {
  cursor: default;

  background: rgb(245, 245, 245);
  color: rgb(180, 180, 180);

  border-color: transparent;

  &:focus {
    box-shadow: none;
    outline: none;
  }
}

.button-bar {
  display: flex;
  flex-direction: row;

  > .button + .button {
    margin-left: 1rem;
  }
}
