html,
body {
  margin: 0;
  padding: 0;
}

body {
  color: black;
  font-family: 'Exo 2', sans-serif;
  font-weight: 300;
  line-height: 2;
}

a {
  text-decoration: none;
}

a:link,
a:active,
a:visited {
  color: rgb(0, 100, 206);
}

a:hover {
  text-decoration: underline;
}

strong {
  font-weight: bold;
}

.container {
  margin: 0 1rem;

  @media(min-width: 80rem) {
    margin: 0 10rem;
  }
}

.row {
  display: grid;
  grid-auto-flow: column;
  gap: 2rem;
}

.row-end {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.col {
  display: block;
}

.logo {
  height: 1.4rem;
}

.block {
  padding: 6rem 0;
}

.block-dark {
  background: rgb(0, 100, 206);
  color: white;

  a,
  a:link,
  a:active,
  a:visited {
    color: inherit;
  }
}

.block-large {
  box-sizing: border-box;


  @media (orientation: landscape) {
    height: 100vh;
  }
}

.block-center {
  >.container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.jumbo-block {
  height: 100vh;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  padding: 6rem 0;

  background-image: url('/assets/jumbo.jpg');
  background-size: cover;
}

.jumbo-text {
  width: 50%;
  height: 25%;

  display: flex;
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  margin-top: 10rem;

  background: rgba(255, 255, 255, 0.75);
  background: white;
  font-size: 1.2rem;

  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

.heading-huge {
  font-size: 3rem;
  font-weight: bold;
}

.markdown h1,
.markdown h2,
.markdown h3,
.markdown h4,
.markdown h5,
.markdown h6,
.heading-1,
.heading-2,
.heading-3,
.heading-4,
.heading-5,
.heading-6 {
  margin-top: 2rem;
  margin-bottom: 1rem;
  line-height: 1;

  &:first-child {
    margin-top: 0;
  }
}

.markdown h1,
.heading-1 {

  margin-bottom: 3rem;

  font-size: 2rem;
  font-weight: bold;
}

.markdown h2,
.heading-2 {
  &:first-child {
    margin-top: 0;
  }

  font-size: 1.4rem;
  font-weight: bold;
}

.markdown h3,
.heading-3 {
  font-size: 1.2rem;
  font-weight: bold;
  margin-bottom: 0;
}

.ul {
  margin: 1rem 0;
  list-style-type: disc;
  list-style-position: inside;

  >li {
    margin: 0.5rem 0;
    margin-left: 1rem;
  }
}

.article {
  margin: 2rem 0;

  &:first-child {
    margin-top: 0;
  }
}

.para {
  margin: 1rem 0;
  max-width: 50rem;
}

.note {
  margin: 2rem 0;
  padding: 1rem;

  background: rgb(245, 245, 245);

  border: 1px solid rgb(200, 200, 200);
  border-radius: 0.2rem;
}

.screenshot {
  display: inline-block;
  box-sizing: border-box;
  width: calc(100vw - 2rem);

  margin-top: 4rem;

  @media(min-width: 40rem) {
    width: 50vw !important;
  }
}

.huge-list {
  display: flex;
  flex-direction: column;

  margin: 4rem 0;

  >li {
    display: flex;
    flex-direction: row;
  }

  >li>i {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-right: 2rem;

    font-size: 4rem;

    color: rgb(0, 100, 206);
  }

  >li {
    margin: 1rem 0;

    &:first-child {
      margin-top: 0;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.news-meta {
  display: grid;
  grid-auto-flow: column;
  grid-template-columns: max-content;
  align-items: center;
  gap: 1rem;

  margin: 0.5rem 0;

  line-height: 1;

  font-size: 0.9rem;

  vertical-align: baseline;
}

.tag-list {
  vertical-align: baseline;

  >li {
    vertical-align: baseline;
    display: inline-block;
    padding: 0.3rem;

    background: rgb(0, 100, 206);
    color: white;

    border-radius: 0.2rem;
  }
}

.tag-list-white {
  >li {
    background: white;
    color: rgb(0, 100, 206);
  }
}
